module.exports = function (_) {
  // Mixing in the truthiness
  // ------------------------
  _.mixin({
    exists: function exists(x) {
      return x != null;
    },
    truthy: function truthy(x) {
      return x !== false && _.exists(x);
    },
    falsey: function falsey(x) {
      return !_.truthy(x);
    },
    not: function not(b) {
      return !b;
    },
    existsAll: function existsAll() {
      return _.every(arguments, _.exists);
    },
    truthyAll: function truthyAll() {
      return _.every(arguments, _.truthy);
    },
    falseyAll: function falseyAll() {
      return _.every(arguments, _.falsey);
    },
    firstExisting: function firstExisting() {
      for (var i = 0; i < arguments.length; i++) {
        if (_.exists(arguments[i])) return arguments[i];
      }
    }
  });
};